<template>
  <div class="videoitem">
    <div class="videonews" v-for="(news,index) in content" :key="index">
        <div class="videoel">
            <video :src="news.video" @click="topaly($event)"></video>
        </div>
        <div class="title" :data-videoid="news.id" @click="clickVideoItem($event)">{{news.title}}</div>
        <div class="time">{{news.time}}</div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import config from "../../base/config";
export default {
  name: 'videoitem',
  props:["content"],
  data () {
    return {
      showpaly:false
    }
  },
  methods:{
      topaly(e){
            let at=$(e.target).attr("controls");
            if(at==undefined){
               e.target.setAttribute("controls", "controls");
            }else{
              $(e.target).removeAttr("controls");
            }
      },
      clickVideoItem(e){
        let videoid=e.target.dataset.videoid;
        localStorage.setItem("isvideo",true);
        localStorage.setItem("videoid",videoid)
        this.$router.push({name:'videopageview', params: { videoid: videoid }})
      }
  },
  mounted(){
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.videoitem{
  position: relative;
    width: 100%;
    height: auto;
    
}
.videoitem .videoel video{
    width: 100%;
    height: 13rem;
    margin: 0;
}
.videoitem .title{
    margin: 0.3rem 1rem 0.5rem;
}
.videoitem .time{
    font-size: 0.8rem;
    margin:0 0 0.7rem 1rem;
}
</style>


